.navbar .nav-items.no-scrollbar {
  padding-right: 15px;
}

.navbar .nav-items .nav-links {
  display: flex;
  flex-direction: row;
  list-style: none;
  transition: 0.5s ease-in;
}

.navbar .nav-items .nav-links .nav-link {
  text-decoration: none;
  padding: 5px 15px;
