  display: none;
  width: 100%;
  transition: 0.2s;
}

.navbar .nav-items .nav-burger .burger {
  transition: 0.8s;
  -webkit-clip-path: polygon(0 100%, 0% 0, 100% 0, 100% 100%);
          clip-path: polygon(0 100%, 0% 0, 100% 0, 100% 100%);
  cursor: pointer;
}

.navbar .nav-items .nav-burger .burger .line {
  background: #2e2e2e;
  height: 5px;
  width: 2.5rem;
