  .navbar.burger-opened .nav-items .nav-burger .burger .line {
    background: #2e2e2e;
    animation: lightenUpBg 0.5s 0.58s forwards;
  }
  .navbar.closing-burger,
  .navbar.scrolled.closing-burger {
    transition: 0.5s ease-in;
  }
  @keyframes lightenUp {
    85% {
      color: white;
    }
    100% {
      color: #b9b9b9;
    }
  }
  @keyframes lightenUpBg {
    85% {
      background: white;
    }
    100% {
      background: #b9b9b9;
    }
  }
}
